import React, { useState } from "react";
import Header2 from "@/Navbar/Header2";

function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <>
      <header id="mad-header" className="mad-header header-2">
        <div className="mad-header-section--sticky-xl">
          <div className="container">
            <div className="row align-items-center">
              {/* Logo */}
              <div className="col-xl-3 col-lg-4 col-sm-3 col-6">
                <a href="/" className="mad-logo">
                  <img src="/img/logo.png" alt="Logo" />
                </a>
              </div>

              {/* ======= Mobile Menu Toggle ======= */}
              <div className="col-6 d-lg-none text-end">
                <button
                  className="mad-cat-toggle"
                  onClick={() => setMenuOpen(!menuOpen)}
                  aria-label="Toggle navigation"
                >
                  ☰
                </button>
              </div>

              {/* ======= Navigation ======= */}
              <div
                className={`col-xl-6 col-lg-4 col-sm-12 ${
                  menuOpen ? "menu-open" : "menu-closed"
                }`}
              >
                <nav className="mad-navigation-container">
                  <ul className="mad-navigation mad-navigation--vertical-sm">
                    <li className="menu-item home-menu mega-menu current-menu-item">
                      <a href="#">Home</a>
                    </li>
                    <li className="menu-item mega-menu">
                      <a href="#">Shop</a>
                    </li>
                    <li className="menu-item">
                      <a href="#">Pages</a>
                    </li>
                    <li className="menu-item mega-menu">
                      <a href="#">Elements</a>
                    </li>
                    <li className="menu-item">
                      <a href="#">Blog</a>
                    </li>
                  </ul>
                </nav>
              </div>

              {/* ======= Right Icons ======= */}
              <div className="col-xl-3 col-lg-4 col-sm-6 d-none d-lg-block">
                <div className="mad-icon-boxes our-info item-col-1">
                  <div className="mad-col mad-header-sticky">
                    {/* Support Info */}
                    <article className="mad-icon-box">
                      <i className="mad-icon-box-icon">
                        <img
                          className="svg"
                          src="/img/support.svg"
                          alt="Support"
                        />
                      </i>
                      <div className="mad-icon-box-content">
                        <span className="mad-icon-sub-title">
                          24/7 Customer Support:
                        </span>
                        <h4 className="mad-icon-box-title">+1 800 559 6580</h4>
                      </div>
                    </article>

                    {/* Action Icons */}
                    <div className="mad-actions">
                      <div className="mad-col">
                        <a href="#">
                          <i className="material-icons">compare_arrows</i>
                          <span>2</span>
                        </a>
                      </div>
                      <div className="mad-col">
                        <a href="#">
                          <i className="material-icons">favorite_border</i>
                          <span>5</span>
                        </a>
                      </div>
                      <div className="mad-col">
                        <a href="shop_account.html">
                          <i className="material-icons">person_outline</i>
                        </a>
                      </div>
                      {/* Cart Dropdown */}
                      <div className="mad-col mad-dropdown">
                        <button className="mad-dropdown-title">
                          <i className="material-icons-outlined">
                            shopping_bag
                          </i>
                          <span>3</span>
                        </button>
                        <span>$49.99</span>
                        <div className="shopping-cart mad-dropdown-element">
                          <div className="mad-products hr-type style-2">
                            {/* Cart Items */}
                            <div className="mad-product">
                              <button className="mad-close-item">
                                <i className="material-icons-outlined">close</i>
                              </button>
                              <div className="mad-product-image">
                                <img src="images/96x96_product1.jpg" alt />
                              </div>
                              <div className="mad-col">
                                <div className="mad-product-description">
                                  <h6 className="mad-product-title">
                                    <a
                                      href="shop_layout_boxed.html"
                                      className="mad-link"
                                    >
                                      John Plunkett Super­Fade Face Cream ...
                                    </a>
                                  </h6>
                                </div>
                                <div className="mad-product-info">
                                  <div className="mad-product-price">
                                    1 x $17.99
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                          <div className="sc-footer">
                            <div className="subtotal">Subtotal: $38.93</div>
                            <div className="btn-set">
                              <a href="#" className="btn btn-small btn-style-7">
                                View Cart
                              </a>
                              <a
                                href="shop_checkout.html"
                                className="btn btn-small"
                              >
                                Checkout
                              </a>
                            </div>
                          </div>
                        </div>
                      </div>
                      {/* End Cart */}
                    </div>
                  </div>
                </div>
              </div>
              {/* End Right Icons */}
            </div>
          </div>
        </div>

        {/* Secondary Header (Browse Categories + Search) */}
        <Header2 />
      </header>
    </>
  );
}

export default Header;
